<template>
  <div class="card">
    <div class="name">{{ item["zhName"] }}</div>
    <div class="sub-name">{{ item.name }}</div>
    <img class="main-variation" v-lazy="mainImage ? mainImage : item.content.image" />
    <div class="variations" v-if="item.variations.length > 0">
      <img
        class="variation"
        :class="{ 'variation--isOwnd': status[obj.idx] }"
        v-lazy="obj.content.image"
        v-for="(obj, i) in item.variations"
        @click="toggleVariation(obj.idx)"
        @mouseover="modifyMainImage(obj.content.image)"
        :key="i"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "card",
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
    index: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      },
    },
  },
  data() {
    return {
      mainImage: "",
    };
  },

  mounted() {},
  methods: {
    toggleVariation(order) {
      const currentStatus = this.status[order];
      this.status[order] = currentStatus ? 0 : 1;
      this.$store.commit("UPDATE_STATUS", this.status);
    },
    modifyMainImage(imgSrc) {
      this.mainImage = imgSrc;
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.card:first-child {
  margin-top: 12px;
}

.card + .card {
  margin-top: 12px;
}

.card {
  width: 296px;
  background: #ecedef;
  padding: 20px 40px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 10px;

  .name {
    font-size: 22px;
    font-weight: bolder;
  }

  .sub-name {
    font-size: 13px;
    color: #bbb;
  }

  .main-variation {
    width: 128px;
    height: 128px;
    transition: 0.6s linear;
    align-self: center;
  }

  .variations {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;

    .variation {
      display: inline-block;
      cursor: pointer;
      width: 43.2px;
      height: 43.2px;

      &.variation--isOwnd {
        background-color: #40474e;
        color: #fff;
        opacity: 1;
      }
    }

    span {
      width: 14px;
      height: 14px;
      margin: 0 10px;
      border-radius: 50%;
      cursor: pointer;
      position: relative;
    }
  }
}
</style>
